* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

$mainBg: #2c2c2c;

.err {
  color: hsl(0, 94%, 73%);
  background-color: white;
  padding: 2rem;
  border-radius: 4px;
  margin-top: 2rem;
}

