  //   border-radius: 6px;
  // }
}
.arrows {
  color: white;
  display: flex;
  width: 85%;
  margin-top: 2rem;
  justify-content: space-between;
  a {
    text-decoration: none;
    color: black;
    display: block;
    opacity: 1;
    background-color: white;
    font-weight: 900;
    padding: 0.4rem 1rem;
    border: none;
    display: flex;
    border-radius: 0.2rem;
    cursor: pointer;
    &.disabled {
      opacity: 0.5;
      pointer-events: none;
    }
    &:hover {
      background-color: rgb(168, 85, 247);
      color: white;
    }
  }
}
.card {
  width: 280px;
  max-height: max-content;
  // background-color: orange;
  display: grid;
  border-radius: 0.4rem;
  grid-template-rows: max-content 1fr;

  .info-wrap {
    display: flex;
    flex-direction: column;
    padding: 0.5rem 1rem 1rem 1rem;
    color: rgba(0, 0, 0, 0.801);
  }
  .title {
    font-size: 1.5rem;
    color: black;
    font-weight: 800;
    margin-bottom: 1rem;
  }
  // .rank {
  //   background-color: white;
  //   padding: 0.5rem 0.8rem;
  //   width: 100%;
  //   border-radius: inherit;
  //   border-top-left-radius: 0;
  //   border-top-right-radius: 0;
  //   text-align: center;
  //   font-size: 2rem;
  //   margin: auto 0 0 0;
  // }
  .img-wrap {
    border-radius: inherit;
    height: 300px;
    overflow: hidden;
    width: 100%;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    img {
      height: 100%;
      width: 100%;
      object-position: 50% 0;
      object-fit: cover;
      width: 100%;
      border-radius: inherit;
    }
  }
}
